.input {
  margin-left: 20px;
  border-radius: 15px;
  width: 200px;
  font-size: 30px;
  padding: 10px 20px;
  box-shadow: 2px 2px 5px rgb(119, 119, 163), -2px -2px 5px rgb(119, 119, 163);
}
.input:focus {
  border: 1px solid rgba(0, 0, 0, 0.25);
}
.label {
  font-size: 35px;
}

.form {
  border: 1px solid rgba(0, 0, 0, 0.25);
  padding: 10px;
  border-radius: 10px;
  box-shadow: 2px 2px 5px rgb(119, 119, 163), -2px -2px 5px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 200px;
}

.button {
  font-size: 30px;
  margin-top: 20px;
  height: 60px;
  transition: 1s;
  width: 60%;
  border-radius: 20px;
  background: red;
  border: none;
}
.button:hover {
  background: blue;
  transition: all 1s;
}

.span {
  font-size: 18px;
  color: red;
  margin-top: 5px;
}

@media screen and (max-width: 400px) {
  .input {
    margin-left: 10px;
    border-radius: 5px;
    width: 200px;
    font-size: 25px;
    padding: 5px 10px;
  }
  .label {
    font-size: 25px;
  }
  .form {
    width: 350px;
    height: 200px;
  }
  .button {
    font-size: 20px;
    height: 30%;
  }
  .span {
  }
}
